.chakra-modal__content {
  background: #536878 !important;
  color: floralwhite !important;
}

.cart-item {
  display: flex;
  column-gap: 25px;
  justify-content: space-between;

  .product-image {
    max-width: 100px;
    max-height: 100px;
    border-radius: 8px;
    margin-top: 15px;
    filter: drop-shadow(0px 2px 3px grey);
  }

  .text-l-font-bold {
    font-size: large;
    font-weight: bold;
  }

  .text-600-small {
    font-size: small;
    font-weight: 600;
  }

  .item-details {
    font-size: smaller;
    font-weight: 300;
    opacity: 80%;
  }
}

.inquire-btn {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.inquire-btn:after {
  content: '>';
  position: absolute;
  opacity: 0;  
  top: 14px;
  right: -20px;
  transition: 0.5s;
}

.inquire-btn:hover{
  padding-right: 24px;
  padding-left:8px;
}

.inquire-btn:hover:after {
  opacity: 1;
  right: 10px;
}

.remove-btn {
  color: lightgray !important;
  align-items: flex-start !important;
  font-size: smaller !important;
}
